import * as React from "react"
import { Link, graphql } from "gatsby"

import Bio from "../components/bio"
import Layout from "../components/layout"
import Seo from "../components/seo"

const BlogPostTemplate = ({
  data: { previous, next, site, markdownRemark: post },
  location,
  pageContext,
}) => {
  const siteTitle = site.siteMetadata?.title || `Title`
  const { series, seriesPosts } = pageContext;

  React.useEffect(() => {
    if (typeof window !== "undefined") {
      const adsbygoogle = window.adsbygoogle || [];
      adsbygoogle.push({});
    }
  }, []);

  return (
    <Layout location={location} title={siteTitle}>
      <article
        className="blog-post"
        itemScope
        itemType="http://schema.org/Article"
      >
        <header>
                {/* Google AdSense Code */}
      <div>
        <ins className="adsbygoogle"
             style={{ display: "block" }}
             data-ad-client="ca-pub-1067668052326016"
             data-ad-slot="8610341899"
             data-ad-format="auto"
             data-full-width-responsive="true"></ins>
        <script
          async
          src="https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js?client=ca-pub-1067668052326016"
          crossOrigin="anonymous"></script>
        <script>
          {`(adsbygoogle = window.adsbygoogle || []).push({});`}
        </script>
      </div>
          <h1 itemProp="headline">{post.frontmatter.title}</h1>
          <p>{post.frontmatter.date}</p>
              
          { series && (
            <div>
              <h2>"{series}" 시리즈 </h2>
              <ul>
                {seriesPosts.map((seriesPost) => (
                  <li key={seriesPost.fields.slug}>
                    <Link to={seriesPost.fields.slug}>{seriesPost.frontmatter.title}</Link>
                  </li>
                ))}
              </ul>
            </div>
          )}
        </header>

        <div>
  <ins className="adsbygoogle"
       style={{ display: "block" }}
       data-ad-client="ca-pub-1067668052326016"
       data-ad-slot="5984178559"
       data-ad-format="auto"
       data-full-width-responsive="true"></ins>
  <script
    async
    src="https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js?client=ca-pub-1067668052326016"
    crossOrigin="anonymous"></script>
  <script>
    {`(adsbygoogle = window.adsbygoogle || []).push({});`}
  </script>
</div>

        <section
          dangerouslySetInnerHTML={{ __html: post.html }}
          itemProp="articleBody"
        />
        <hr />
        <footer>
          <Bio />
        </footer>
      </article>

      <nav className="blog-post-nav">
        <ul
          style={{
            display: `flex`,
            flexWrap: `wrap`,
            justifyContent: `space-between`,
            listStyle: `none`,
            padding: 0,
          }}
        >
          <li>
            {previous && (
              <Link to={previous.fields.slug} rel="prev">
                ← {previous.frontmatter.title}
              </Link>
            )}
          </li>
          <li>
            {next && (
              <Link to={next.fields.slug} rel="next">
                {next.frontmatter.title} →
              </Link>
            )}
          </li>
        </ul>
      </nav>
    </Layout>
  )
}

export const Head = ({ data: { markdownRemark: post } }) => {
  return (
    <Seo
      title={post.frontmatter.title}
      description={post.frontmatter.description || post.excerpt}
    />
  )
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug(
    $id: String!
    $previousPostId: String
    $nextPostId: String
  ) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(id: { eq: $id }) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        description
        series
      }
    }
    previous: markdownRemark(id: { eq: $previousPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    next: markdownRemark(id: { eq: $nextPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
  }
`